/* ================= THEME (kept) ================= */
:root {
  --c1: #09162b;
  --c2: #1a3463;
  --c3: #23293b;
  --radius: 16px;
  --gutter: 14px;
}

/* ================= BACKGROUND + ANIMATION ================= */
html { scroll-behavior: smooth; }
body {
  font-family: 'Inter', system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  color: #fff;
  margin: 0;
  background: linear-gradient(135deg, var(--c1), var(--c2), var(--c3));
  background-size: 400% 400%;
  animation: gradientShift 12s ease infinite;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@keyframes gradientShift {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* ================= FROSTED / GLASS PANEL ================= */
.frosted {
  background-color: rgba(18, 27, 40, 0.62);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1.5px solid rgba(0,191,255,0.12);
  box-shadow:
    0 12px 40px 0 rgba(38,112,255,0.08),
    -4px 0 24px rgba(0,191,255,0.06);
  color: #e6f7ff;
}

/* Slightly darker tiles for placeholders/video containers */
.tile {
  background: rgba(9, 23, 43, 0.75);
  border: 1px solid rgba(20, 56, 108, 0.35);
}

/* initials bubble inside tile */
.initials {
  background: linear-gradient(180deg, rgba(26,52,99,1), rgba(9,22,43,1));
  color: #e6f7ff;
  border: 2px solid rgba(64,231,255,0.14);
}

/* nameplate on tiles */
.nameplate {
  background: rgba(9,22,43,0.7);
  border: 1px solid rgba(20,56,108,0.36);
  color: #e6f7ff;
  box-shadow: 0 6px 18px rgba(10,30,70,0.12);
}

/* small glowing badge */
.badge {
  background: linear-gradient(90deg, #3fe7ff, #26d6ff);
  box-shadow: 0 0 10px rgba(64,231,255,0.22);
}

/* media icons chip */
.iconchip {
  background: rgba(10,20,36,0.45);
  border: 1px solid rgba(20,56,108,0.28);
  padding: 6px 8px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  color: #dff7ff;
}

/* participant list row */
.participant {
  background: rgba(12,24,40,0.6);
  border: 1px solid rgba(20,56,108,0.24);
  color: #e6f7ff;
}

/* controls bar */
.controls {
  background: transparent;
}

/* ctrl buttons default style (keeps Tailwind structure but overrides colors) */
.ctrl {
  min-width: 44px;
  min-height: 44px;
  font-weight: 400;
  border-radius: 999px;
}

/* secondary ctrl - muted look */
.ctrl.secondary {
  background: rgba(9,20,36,0.55);
  color: #d5f6ff;
  border: 1px solid rgba(20,56,108,0.18);
  box-shadow: none;
}

/* danger ctrl - leave button */
.ctrl.danger {
  background: linear-gradient(90deg, #ff5175 0%, #ff1d52 100%);
  color: #0a0b12;
  box-shadow: 0 6px 18px rgba(255,77,120,0.12);
}

/* hide small labels under narrow screens (tailwind also hides .lbl on small) */
.ctrl .lbl { display: inline-block; }
@media (max-width: 768px) {
  .ctrl .lbl { display: none; }
}

/* chat panel drawer (mobile) - default hidden off-screen */
.chat-panel {
  transform: translateX(100%);
  transition: transform 260ms cubic-bezier(.2,.9,.2,1);
  /* keep rounded and frosted */
  border-left: 1px solid rgba(20,56,108,0.12);
}

/* when main has chat-open, slide it in */
.chat-open .chat-panel {
  transform: translateX(0);
}

/* on large screens chat-panel becomes static column */
@media (min-width: 1024px) {
  .chat-panel {
    transform: none !important;
    position: static;
    inset: auto;
    width: auto;
    max-width: none;
  }
}

/* chat header and messages */
.chat-header {
  background: transparent;
  color: #e6f7ff;
}

.chat-body .msg {
  background: rgba(8,20,36,0.55);
  border: 1px solid rgba(20,56,108,0.16);
  color: #dff7ff;
}

/* chat input */
.chat-input input.dark-input,
input.dark-input,
.floating-input {
  background: rgba(0,0,0,0.28);
  color: #e9fbff;
  border: 1px solid rgba(148,163,184,0.08);
  padding: 10px 12px;
  outline: none;
  transition: box-shadow .18s ease, border-color .18s ease;
  border-radius: 999px;
}

/* input focus glow */
input.dark-input:focus,
.floating-input:focus {
  box-shadow: 0 0 0 6px rgba(15,217,255,0.06), 0 4px 18px rgba(10,40,80,0.12);
  border-color: #0fd9ff;
}

/* floating label specifics */
.floating { position: relative; }
.floating .icon-left { pointer-events: none; position: absolute; left: 0.75rem; top: 50%; transform: translateY(-50%); color: #9aa6bb; }
.floating .floating-label {
  position: absolute;
  left: 2.5rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1rem;
  color: #9aa6bb;
  pointer-events: none;
  transition: all 180ms ease;
}
.floating input.floating-input:focus + label.floating-label,
.floating input.floating-input:not(:placeholder-shown) + label.floating-label {
  top: 0.3rem;
  transform: translateY(0);
  font-size: 0.78rem;
  color: #0fd9ff;
}

/* small UI helpers */
.hr-btn {
  background: linear-gradient(90deg, #0fd9ff 0%, #2e57ff 100%);
  color: #0a0b12;
  border-radius: 999px;
  padding: 6px 10px;
  font-weight: 700;
  box-shadow: 0 6px 18px rgba(40,75,255,0.12);
}

/* tile placeholder sizing helpers */
.tile .placeholder { display: grid; place-items:center; width:100%; height:100%; }
.tile.video-on video { display: block; }

/* Accessibility: visually hidden helper */
.visually-hidden {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
}

/* responsive tweaks */
@media (max-width: 1024px) {
  .sidebar { max-height: calc(100vh - 6rem); }
  .tile-grid { gap: 12px; }
}

/* ensure svg icons sizing if inline svgs are used */
.svg { width: 20px; height: 20px; display: block; }
.no-scrollbar { -ms-overflow-style: none; scrollbar-width: none; }
.no-scrollbar::-webkit-scrollbar { display: none; }
